

































































































import footerNav from "@/components/footer.vue"
import uniDrawer from '@/components/uni-drawer/uni-drawer.vue'
import {
    mapState,
    mapActions
} from 'vuex'
export default {
	data() {
		return {
			loading: false,
			goodsName: '',
			userName: '',
			orderNo: '',
			phone: '',
			address: '',
			showRight: false,
			orderList: []
		}
	},
	components: {
		footerNav,
		uniDrawer
	},
	computed: {
		...mapState({
		  urlPre: state => state.urlPre,
		  purchaseUnit: state => state.purchaseUnit
		})
	},
	onLoad: function () {
		this.search()
	},
	onPullDownRefresh: async function () {
		this.search()
	},
	methods: {
		// 重置查询条件
		reset: function () {
			this.goodsName = ''
			this.userName = ''
			this.orderNo = ''
			this.phone = ''
			this.address = ''
		},
		// 查询
		search: function() {
			uni.request({
				url: this.urlPre + '/page/getByywy.do?purchase=' + this.purchaseUnit + (this.goodsName ? '&fname1=' + this.goodsName: '') + (this.userName ? '&fcontact=' + this.userName: '') + (this.orderNo?'&fbillno=' + this.orderNo: '') + (this.phone?'&fmobile=' + this.phone: '') + (this.address?'&fdeliveryaddress=' + this.address : ''),
				method: 'GET',
				data: {
				},
				header: {
				  'content-type': 'application/x-www-form-urlencoded'
				},
				success: (res) => {
					switch (res.data.result) {
						case 1:
							this.orderList = res.data.orderList.map(item => {
								if (item.bpmstatus == 1) {
									item.bpmstatusTxt = '未排单'
								}
								if (item.bpmstatus == 2 || item.bpmstatus == 'q') {
									item.bpmstatusTxt = '待结单'
								}
								if (item.bpmstatus == 3 || item.bpmstatus == 4) {
									item.bpmstatusTxt = '待预约'
								}
								if (item.bpmstatus == 5 || item.bpmstatus == 6 || item.bpmstatus == 7 || item.bpmstatus == 8) {
									item.bpmstatusTxt = '上门服务'
								}
								if (item.bpmstatus == 'A') {
									item.bpmstatusTxt = '已完成'
								}
								return item
							})
							break
						default:
							uni.showToast({
								image: '/static/icons/attention.png',
								title: '订单获取失败!'
							})
					}
					
				},
				fail: (err) => {
					console.log('request fail', err);
					uni.showModal({
						content: err.errMsg,
						showCancel: false
					});
				},
				complete: () => {
					uni.stopPullDownRefresh()
					uni.hideLoading()
				}
			})
		},
		// 展开查询条件抽屉
		show(e) {
			if (e === 'left') {
			} else {
				this.showRight = true
			}
		},
		// 关闭查询条件抽屉
		hide() {
			this.showRight = false
		},
		// 监听抽屉关闭
		closeDrawer(e) {
			this.search()
			this.showRight = false
		}
	},
	// 监听导航按钮事件
	onNavigationBarButtonTap(e) {
		this.showRight = !this.showRight
	},
	// 监听导航返回事件
	onBackPress() {
		if (this.showRight) {
			this.hide()
			return true
		}
	}
}
